import React from 'react';
import {public_url} from '../../../../../config'
import Moment from 'moment';
import IconButton from '@material-ui/core/IconButton'

const ConversationCell = ({conversation}) => {
  return (
    <div className="d-flex module-detail-item">
      <div className="chat-todo-avatar">
        <img className="rounded-circle avatar size-40" src={`${public_url}upload/img/${conversation.profile_pic}`}
             alt="..."/>
      </div>
      <div className="chat-toto-info">
        <div className="d-flex  flex-column">
          <div className="name mr-2">{`${conversation.first_name} ${conversation.last_name}`}</div>
          <div className="time text-muted">{Moment(conversation.created_at).format('ddd DD, YYYY, hh:mm:ss A')}</div>
        </div>
        <div className="message">{conversation.comment}</div>
      </div>
      <div className="jr-featured-content-right">
      {
        conversation.attach_file ? (
          <a href = {`${public_url}upload/file/${conversation.attach_file}`}>
          <IconButton className="icon-btn text-success">
            <i className="zmdi zmdi-file"/>
          </IconButton>
          </a>
        ) : (
          <div></div>
        )
      }
      </div>
    </div>
  )
};

export default ConversationCell;