import React from 'react';
import RoomTableCell from './RoomTableCell';
import IntlMessages from 'util/IntlMessages';

const RoomTable = (props) => {

    const {onSelectRoom} = props;

    return (
        props.Rooms && (
            <div className="table-responsive-material">
                <table className="default-table table-unbordered table table-sm table-hover">
                    <thead className="th-border-b">
                    <tr>
                        <th>Room</th>
                        <th>Site</th>
                        {/* <th>Ceiling Height</th>
                        <th>Walls</th> */}
                        <th className="text-right">Actions</th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        props.Rooms.length > 0 ?
                            props.Rooms.map(data => {
                                return (
                                    <RoomTableCell key={data.id} data={data} onSelectRoom={onSelectRoom}/>
                                );
                            })
                        :
                        <tr>
                            <td className="text-danger" colSpan="6">
                                <IntlMessages id="table.noData"/>
                            </td>
                        </tr>
                    }
                    </tbody>
                </table>
            </div>
        )
    );
};

export default RoomTable;