import React, {useState} from 'react';
import Checkbox from '@material-ui/core/Checkbox';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton'
import {SortableElement, SortableHandle} from 'react-sortable-hoc';
import {public_url} from "config";
// This can be any component you want

const TaskItem = SortableElement(({Task, onTaskSelect, onTaskChecked, onMarkAsStart}) => {
    const [favourite, setFavourite] = useState(Task.favourite == '1')
    const [archived, setArchived] = useState(Task.archived == '1')

    return (
        <div className={`module-list-item ${Task.is_require ? 'bg-grey':''}`}>
            <div className="module-list-icon">
                <Checkbox color="primary"
                    checked={Task.archived == '1'}
                    onClick={(event) => {
                        Task.archived = Task.archived == '1' ? '0' : '1'
                        setArchived(!archived)
                        onTaskChecked(Task)
                    }}
                    value="SelectTask"
                />

                <IconButton className="icon-btn" onClick={() => {
                    Task.favourite = Task.favourite == '1' ? '0' : '1'
                    onMarkAsStart(Task, Task.favourite)
                    setFavourite(!favourite)
                }}>
                    {Task.favourite == '1' ?
                        <i className="zmdi zmdi-star text-warning"/> :
                        <i className="zmdi zmdi-star-outline text-warning"/>
                    }
                </IconButton>
            </div>
            <div className="module-list-info mr-2" onClick={() => {
                onTaskSelect(Task);
            }}>
                <div className='row'>
                    <div className="module-todo-content col-8 col-sm-6 col-md-6">
                        <div className={`subject ${Task.archived=='1' && 'text-muted text-strikethrough'}`}>
                            {Task.task}
                        </div>
                        <div className="manage-margin">    
                            <div className={`badge text-white bg-light-blue darken-2`}>{Task.company_name}</div>
                            <div className={`badge text-white bg-green darken-1`}>{Task.project_name}</div>
                            <div className={`badge text-white bg-primary darken-1`}>{Task.room_number}</div>
                        </div>
                    </div>
                    <div className="module-todo-right col-4 col-sm-5 col-md-6">
                        <div className="d-flex flex-row-reverse text-right">
                            <Avatar alt={Task.profile_pic}
                                src={public_url + 'upload/img/' + Task.profile_pic}/>
                            {Task.assign_users.map(item=>(
                            <Avatar 
                            key = {item.id}
                            className ='mr-2'
                            alt={item.profile_pic}
                                src={public_url + 'upload/img/' + item.profile_pic}/>
                            ))}
                        </div>
                        
                    </div>
                </div>
            </div>
        </div>

    )
});

export default TaskItem;