import React, {useState,useEffect} from 'react';
import {useSelector} from 'react-redux';
import Drawer from '@material-ui/core/Drawer';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import filters from '../data/filters';
import TaskList from './TaskList';
import TaskDetail from './TaskDetail';
import CircularProgress from '@material-ui/core/CircularProgress';
import IntlMessages from 'util/IntlMessages';
import CustomScrollbars from 'util/CustomScrollbars';
import {useHistory} from 'react-router-dom';
import {base_url,public_url, $http} from "config";
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import SweetAlert from 'react-bootstrap-sweetalert'
import Menu from '@material-ui/core/Menu';
import MenuIcon from '@material-ui/icons/Menu'
import Typography from '@material-ui/core/Typography'
import { jsPDF } from "jspdf";
import { CSVLink } from "react-csv";
import Accordion from '@material-ui/core/Accordion'
import { AccordionSummary, AccordionDetails, AccordionActions } from '@material-ui/core'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import BrushIcon from '@material-ui/icons/Brush';
import Divider from '@material-ui/core/Divider';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/Inbox';

const drawerWidth = 240;

const styles = theme => ({
    root: {
      width: '100%',
      height: '100%',
      zIndex: 1,
      overflow: 'hidden',
    },
    appFrame: {
      position: 'relative',
      display: 'flex',
      width: '100%',
      height: '100%',
    },
    appBar: {
      position: 'absolute',
      transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
    },
    appBarShift: {
      marginLeft: drawerWidth,
      width: `calc(100% - ${drawerWidth}px)`,
      transition: theme.transitions.create(['margin', 'width'], {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
    menuButton: {
      marginLeft: 12,
      marginRight: 20,
    },
    hide: {
      display: 'none',
    },
    drawerPaper: {
      position: 'relative',
      height: '100%',
      width: drawerWidth,
    },
    drawerHeader: {
      display: 'flex',
      alignItems: 'center',
      justifyContent: 'flex-end',
      padding: '0 8px',
      ...theme.mixins.toolbar,
    },
    content: {
      width: '100%',
      marginLeft: -drawerWidth,
      flexGrow: 1,
      padding: theme.spacing(3),
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      marginTop: 56,
      [theme.breakpoints.up('sm')]: {
        content: {
          height: 'calc(100% - 64px)',
          marginTop: 64,
        },
      },
    },
    contentShift: {
      marginLeft: 0,
      transition: theme.transitions.create('margin', {
        easing: theme.transitions.easing.easeOut,
        duration: theme.transitions.duration.enteringScreen,
      }),
    },
  });

const Task = (props) => {
    const nowuser = JSON.parse(localStorage.getItem("user"));
    const {projectId, roomId, customerId,room,task,panelOpen,end_enable} = props
    const [searchTask, setSearchTask] = useState('');
    const [loader, setLoader] = useState(false);
    const [allTasks, setAllTasks] = useState([]);
    const [Tasks, setTasks] = useState([]);
    const [users, setUsers] = useState([]);
    const [customers, setCustomers] = useState([]);
    const [projects, setProjects] = useState([]);
    const [user_id, setUserId] = useState(0);
    const [customer_id, setCustomerId] = useState(0);
    const [project_id, setProjectId] = useState(0);
    const [selectedSectionId, setSelectedSectionId] = useState("");
    const [allSelect, setAllSelect] = useState(1);
    const {width} = useSelector(({settings}) => settings);
    const [drawerState, setDrawerState] = useState(false);
    const [currentTask, setCurrentTask] = useState(task?task:null);
    const [goback, setGoback] = useState(false)
    const [taskMenu, setTaskMenu] = useState(false)
    const [anchorEl,setAnchorEl] = useState('')
    const onMarkAsStart = (data, favourite) => {
        const frmData = new FormData();
        frmData.append('id', data.id);
        frmData.append('favourite', favourite);
        $http.post(base_url + "task/setFavourite", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                if (favourite) {
                    NotificationManager.info(`Task ${data.task} is set to favourite one!`);
                } else {
                    NotificationManager.info(`Task ${data.task} is released from favourite one!`);
                }
                // init_Data();
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                });
            }
            
        }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
        })
    };

    useEffect(() => {
        init_Data();
    }, []);
    const History = useHistory();
    const init_Data = () => {
        $http.get(base_url + "task/taskList", {params: { project_id:projectId,room_id:roomId, customer_id: customerId}})
        .then((response) => {
            //console.log(response.data.tasks)
            setAllTasks(response.data.tasks)
            setTasksFilter(selectedSectionId, user_id, customer_id, project_id, response.data.tasks)
            setUsers(response.data.users)
            setCustomers(response.data.customers)
            setProjects(response.data.projects)
            setCustomerId(response.data.customerId)
            setProjectId(projectId)
            setLoader(true)
        }).catch((error) => {
            setGoback(true)
          })
    }

    
    const addTask=()=>{
        if(nowuser.user_type ==6 && end_enable =='0') {
            NotificationManager.error("You can't add new task!", "Error!", 1000, () => {
            });
            return
        }
        //History.push('/app/task-manager/add-new');
        History.push({
            pathname: '/app/task-manager/add-new',
            state: {
                room_id: roomId,
                project_id: project_id,
                customer_id: customer_id
            },
        })

    }
    const setTasksFilter = (s_id, u_id, c_id, p_id, tasks = allTasks) => {
        if (s_id == undefined) s_id = ''
        if (u_id == undefined) u_id = 0
        if (c_id == undefined) c_id = 0
        if (p_id == undefined) p_id = 0
        //setCurrentTask(null)
        if (s_id || u_id || c_id || p_id) {
            setAllSelect(0)
        } else {
            setAllSelect(1)
        }
        setTasks(tasks.filter(Task => {
            return ((s_id === '') 
                || (s_id === 1 && Task.favourite === '1') 
                || (s_id === 2 && Task.priority === '1') 
                || (s_id === 3 && Task.archived !== '1')
                || (s_id === 4 && Task.archived === '1'))
            && ((u_id == 0 && Task.created_by > 0) 
                || (u_id > 0 && Task.created_by == u_id))
            && ((c_id == 0 && Task.company_id > 0) 
                ||(c_id > 0 && Task.company_id == c_id))
            && ((p_id == 0 && Task.project_id > 0) 
                || (p_id > 0 && Task.project_id == p_id))
        }))
    }

    const getNavFilters = () => {
        return filters.map((filter, index) =>
            <li key={index} onClick={() => {
                setSelectedSectionId(filter.id)
                setTasksFilter(filter.id, user_id, customer_id, project_id)
            }}>
                <span className={`jr-link ${filter.id === selectedSectionId ? 'active' : ''}`}>
                    <i className={`zmdi zmdi-${filter.icon}`}/>
                    <span>{filter.title}</span>
                </span>
            </li>
        )
    }
    const selectUser=(id)=>{
        setUserId(id)
        setTasksFilter(selectedSectionId, id, customer_id, project_id)
    }
    const selectCustomer=(id)=>{
        setCustomerId(id)
        setTasksFilter(selectedSectionId, user_id, id, project_id)
    }
    const selectProject=(id)=>{
        setProjectId(id)
        setTasksFilter(selectedSectionId, user_id, customer_id, id)
    }
    const printTask = ()=>{
        
        var is_upload = nowuser.is_upload >= 0?nowuser.is_upload:1;
        var front_cover = nowuser.front_cover
        var back_cover = nowuser.back_cover
        var doc = new jsPDF('p', 'pt', 'letter');
        var width = doc.internal.pageSize.getWidth();
        var height = doc.internal.pageSize.getHeight();
        var y0 = 200
        var y0 = 200
        var img = new Image();
        var img_ext = '';
        if(is_upload && front_cover){
            //img.src = '/pdf_front.png';
            img.src = public_url + 'upload/img/' + front_cover
            doc.addImage(img, 'png', 0, 0, width, height);
            doc.setFontSize(30);
            doc.text('Scope of works:', 250, y0);
            if(room){
                img.src = public_url + 'upload/img/' + room.logo_img 
                img_ext = room.logo_img .split('.').pop()
                doc.addImage(img, img_ext, 250, y0+30, 200, 130);
                y0 += 200
                doc.setFontSize(12);
                doc.text("Client: ", 250, y0);
                doc.text(room.company_name+'', 350, y0);
                doc.text("Project Title: ", 250, y0+30);
                doc.text(room.project_name+'', 350, y0+30);
                doc.text("Room Number: ", 250, y0+60);
                doc.text(room.room_number+'', 350, y0+60);
                doc.text("Sirvey Date: ", 250, y0+90);
                doc.text(room.survey_start_date+'', 350, y0+90);
                doc.text("site Name: ", 250, y0+120);
                doc.text(room.site_name+'', 350, y0+120);
            }
            doc.addPage();
        }
            y0 =80
             doc.setFontSize(30);
            doc.text("Task List ", 250, y0);
            y0+=50
            doc.setFontSize(13);
            doc.text('-   Tasks', 50, y0);
            y0 = y0+30
            doc.setFontSize(11);
            doc.text('No', 70, y0);
            doc.text('Task', width/6, y0);
            doc.text('Description', width/2, y0);
            y0 = y0+20
            let shift = 0;
            Tasks.map((task,index)=>{
                if(y0+20*(index-shift) > height-100)
                {
                    doc.addPage();
                    y0 = 80;
                    shift = index;
                }
                doc.text(index+1+'', 70, y0+20*(index-shift));
                doc.text(task.task+'', width/6, y0+20*(index-shift));
                doc.text(task.description+'', width/2, y0+20*(index-shift));
            })
        if(is_upload && back_cover){
        doc.addPage();
            //img.src = '/pdf_back.png';
            img.src = public_url + 'upload/img/' + back_cover
            doc.addImage(img, 'png', 0, 0, width, height);
        }
        doc.save("TaskList.pdf");

    }
    const exportTask = ()=>{
        document.getElementById('exportTasks').click()
    }
    const task_headers = [
        { label: "Task Id", key: "id" },
        { label: "Task Title", key: "task" },
        { label: "Task Description", key: "description" },
        { label: "Customer", key: "company_name" },
        { label: "Site", key: "site_name" },
        { label: "Room Number", key: "room_number" },
        { label: "Assign to", key: "assign_to" },
        { label: "Comments(Number)", key: "comment_number" },
        { label: "Comment1", key: "comment1" },
        { label: "Comment1_date", key: "comment1_date" },
        { label: "Comment1_user", key: "comment1_user" },
        { label: "Comment2", key: "comment2" },
        { label: "Comment2_date", key: "comment2_date" },
        { label: "Comment2_user", key: "comment2_user" },
        { label: "Comment3", key: "comment3" },
        { label: "Comment3_date", key: "comment3_date" },
        { label: "Comment3_user", key: "comment3_user" },
    ];
    const csvReport = {
        data: Tasks,
        headers: task_headers,
        filename: 'Task_export.csv'
      };
    const TaskMenuClick = (event) => {
        setTaskMenu(true)
        setAnchorEl(event.currentTarget)
    }
    const TaskMenuClose = () => {
        setTaskMenu(false)
      }

    const TaskSideBar = (isMobile) => {
    return <div className={`module-side ${styles.drawerPaper}`}>
                {
                    isMobile ?
                    <div className="module-side-header">
                        <div className="module-logo">
                            <BrushIcon className="mr-2"/>
                            <span><IntlMessages id="sidebar.taskmanager.mytasks"/></span>
                        </div>
                    </div>
                    : null
                }
                <CSVLink 
                    id = "exportTasks"
                    {...csvReport }
                    style={{display: "none"}}                                            
                >Export to CSV
                </CSVLink>
                <div className={`module-side-content ${styles.drawerInner}`}>
                    <CustomScrollbars className="module-side-scroll scrollbar"
                                    style={{height: width >= 1200 ? 'calc(100vh - 200px)' : 'calc(100vh - 80px)'}}>
                   
                    <div className="module-add-task">
                        <Button variant="contained" color="primary" className="btn btn-primary btn-block mr-0"
                            onClick={()=>{TaskMenuClose() 
                                        addTask()
                            }}>
                            New Task
                        </Button>
                        <Button variant="contained" color="primary" className="btn btn-primary btn-block"
                            onClick={(event)=>{TaskMenuClick(event)}}>
                                Actions
                        </Button>
                        <Menu
                            id="Task-menu"
                            open={taskMenu}
                            anchorEl={anchorEl}
                            onClose={()=>{TaskMenuClose()}}
                        >
                            {/* {nowuser.user_type<6?
                                <MenuItem onClick={() => {TaskMenuClose()
                                    addTask()
                                    }}>New Task
                                </MenuItem>
                            : null } */}
                            
                            <MenuItem onClick={()=>{TaskMenuClose() 
                                printTask()}}>Print
                            </MenuItem>
                            
                            <MenuItem onClick={()=>{TaskMenuClose()
                                exportTask()}}>Export
                            </MenuItem>
                        </Menu>
                         
                    </div>
                    <Divider/>
                    <List className={styles.list}>
                        <ListItem className="module-nav-label">
                            <TextField
                                id="user"
                                select
                                label="User"
                                value={user_id}
                                onChange={(event)=>selectUser(event.target.value)}
                                margin="normal"
                                fullWidth
                            >
                                <MenuItem value={0}>
                                    <IntlMessages id="Task.all"/>
                                </MenuItem>
                                {users.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.first_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </ListItem>
                        {!projectId?
                        ( !customerId ? 
                        (<div>
                            <ListItem className="module-nav-label">
                            <TextField
                                id="customer"
                                select
                                label="Customer"
                                value={customer_id}
                                onChange={(event)=>selectCustomer(event.target.value)}
                                margin="normal"
                                fullWidth
                            >
                                <MenuItem value={0}>
                                    <IntlMessages id="Task.all"/>
                                </MenuItem>
                                {customers.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </ListItem>

                        <ListItem className="module-nav-label">
                            <TextField
                                id="project"
                                select
                                label="Project"
                                value={project_id}
                                onChange={(event)=>selectProject(event.target.value)}
                                margin="normal"
                                fullWidth
                            >
                                <MenuItem value={0}>
                                    <IntlMessages id="Task.all"/>
                                </MenuItem>
                                {projects.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.project_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </ListItem>
                        </div> ) : (
                        <ListItem className="module-nav-label">
                            <TextField
                                id="project"
                                select
                                label="Project"
                                value={project_id}
                                onChange={(event)=>selectProject(event.target.value)}
                                margin="normal"
                                fullWidth
                            >
                                <MenuItem value={0}>
                                    <IntlMessages id="Task.all"/>
                                </MenuItem>
                                {projects.map(option => (
                                    <MenuItem key={option.id} value={option.id}>
                                        {option.project_name}
                                    </MenuItem>
                                ))}
                            </TextField>
                        </ListItem>
                    )
                        ):
                                null
                        }
                        <ListItem button 
                            className="py-0"
                        onClick={() => {
                            setTasks(allTasks)
                            setUserId(0)
                            setCustomerId(0)
                            setProjectId(0)
                            setSelectedSectionId('')
                            setAllSelect(1)
                        }
                        }>
                            <ListItemIcon>
                                <IconButton className={`icon-btn ${allSelect === 1 ? 'text-primary' : ''}`}>
                                    <i className={`zmdi zmdi-view-list zmdi-hc-fw`}></i>
                                </IconButton>
                            </ListItemIcon>
                            <ListItemText primary="All" className={`${allSelect === 1 ? 'text-primary' : ''}`}/>
                        </ListItem>
                        
                        {filters.map((filter, index) =>
                            <ListItem button 
                                className="py-0"
                                key={index} onClick={() => {
                                setSelectedSectionId(filter.id)
                                setTasksFilter(filter.id, user_id, customer_id, project_id)
                            }}>
                                <ListItemIcon>
                                    <IconButton className={`icon-btn ${filter.id === selectedSectionId ? 'text-primary' : ''}`}>
                                        <i className={`zmdi zmdi-${filter.icon}`}/>
                                    </IconButton>
                                </ListItemIcon>
                                <ListItemText primary={filter.title} className={`${filter.id === selectedSectionId ? 'text-primary' : ''}`}/>
                            </ListItem>
                        )}
                    </List>
                    </CustomScrollbars>
                </div>
            </div>
    };
    const onSearchTask = (searchText) => {
        setCurrentTask(null)
        setTasks(allTasks.filter(Task => {
            return ((selectedSectionId === '') || (selectedSectionId === 1 && Task.favourite === '1') 
                || (selectedSectionId === 2 && Task.priority === '1') || (selectedSectionId === 3 && Task.archived !== '1') 
                || (selectedSectionId === 4 && Task.archived === '1')) && 
            ((user_id === 0 && Task.created_by > 0) 
                || (user_id > 0 && Task.created_by == user_id)) &&
            ((customer_id === 0 && Task.company_id > 0) 
                || (customer_id > 0 && Task.company_id == customer_id)) &&
            ((project_id === 0 && Task.project_id > 0) 
                || (project_id > 0 && Task.project_id == project_id)) &&
            ((searchText === '') 
                || (Task.task.toLowerCase().indexOf(searchText.toLowerCase()) > -1))
        }))
    };
  

    const onTaskChecked = (data) => {
        var is_complete = 0;
        is_complete = data.archived === '1' ? 1 : 0;
        const frmData = new FormData();
        frmData.append('id', data.id);
        frmData.append('archived', is_complete);
        $http.post(base_url + "task/setCompleted", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                if (is_complete) {
                    NotificationManager.info(`Task ${data.task} is completed!`);
                } else {
                    NotificationManager.info(`Task ${data.task} is not completed!`);
                }
                init_Data();
            } else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                });
            }
            
        }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
        })
    };
    
    const onTaskSelect = (Task) => {
       setCurrentTask(Task)
    };

    const onToggleDrawer = () => {
        setDrawerState(!drawerState);
    };

    const updateSearch = (evt) => {
        setSearchTask(evt.target.value);
        onSearchTask(evt.target.value)
    };

    const navMenuClick = (e) => {
        e.stopPropagation();
        onToggleDrawer();
    }
    const task_panel=(event)=>{
        if (event.currentTarget.className.indexOf('expanded') > 0) {
          if(roomId)
            localStorage.setItem("room_task",false)
          else if(projectId)
            localStorage.setItem("pro_task",false)
        } else {
          if(roomId)
            localStorage.setItem("room_task",true)
          else if(projectId)
            localStorage.setItem("pro_task",true)
        }
      }

    return (
        <Accordion defaultExpanded={(projectId == null && roomId == null)||panelOpen}>
            <AccordionSummary 
                className ='bg-primary' 
                expandIcon={<ExpandMoreIcon className="text-white size-30"/>}
                onClick={(event) => task_panel(event)}
            >
                <div className="text-white d-flex align-items-center">
                    <IconButton
                    aria-label="open drawer"
                    onClick={(event) => navMenuClick(event)}
                    className="d-block d-xl-none d-lg-none"
                    >
                    <MenuIcon className="text-white size-30"/>
                    </IconButton>
                    <IconButton className="d-none d-xl-block d-lg-block">
                    <BrushIcon className="text-white size-30"/>
                    </IconButton>
                    <Typography variant="h6" color="inherit" noWrap className="text-white mb-0">
                        Tasks -  {Tasks.length}
                    </Typography>
                </div>
            </AccordionSummary>
            <AccordionDetails className="p-0">
                <div className="app-wrapper">
                    <div className="animated slideInUpTiny animation-duration-3">
                        <div className="app-module">
                            <div className="d-block d-xl-none d-lg-none">
                                <Drawer
                                    open={drawerState}
                                    onClose={onToggleDrawer}>
                                    {TaskSideBar(1)}
                                </Drawer>
                            </div>
                            <div className="app-module-sidenav d-none d-xl-flex d-lg-flex">
                                {TaskSideBar(0)}
                            </div>
                            <div className="module-box">
                                {/* <div className="module-box-header bg-primary text-white rounded-top" style={{minHeight: '60px'}}>
                                    
                                    <IconButton className="drawer-btn d-block d-xl-none text-white border-right-0" aria-label="Menu"
                                                onClick={onToggleDrawer}>
                                        <i className="zmdi zmdi-menu"/>
                                    </IconButton>

                                    <AppModuleHeader placeholder="Search Task"
                                                    onChange={updateSearch}
                                                    value={searchTask}/>
                                </div> */}
                                { loader ?
                                    currentTask === null ?
                                        <div className="module-box-content">
                                            <div className="module-box-topbar d-flex align-items-center">
                                                <IconButton className="icon-btn">
                                                    <i className="zmdi zmdi-check"/>
                                                </IconButton>
                                                {`${Tasks.filter(item => item.archived == '1').length} of ${Tasks.length} number of Tasks have been completed.`}
                                            </div>
                                            <TaskList Tasks={Tasks} 
                                            onMarkAsStart={onMarkAsStart}
                                            width={width}
                                            onTaskSelect={onTaskSelect}
                                            onTaskChecked={onTaskChecked} useDragHandle={true}/>
                                        </div>
                                        :
                                        <div className="module-box-content">
                                            <div className="module-box-topbar d-flex align-items-center">
                                                <IconButton className="icon-btn"
                                                            onClick={() => {
                                                                setCurrentTask(null);
                                                            }}>
                                                    <i className="zmdi zmdi-arrow-back"/>
                                                </IconButton>
                                                <h2 className="mb-0">{`task: ${currentTask.task}`}</h2>
                                            </div>
                                            <TaskDetail Task={currentTask}
                                                width={width}
                                                users={users}
                                            />
                                        </div>
                                :
                                    <div className="loader-view"
                                        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                                        <CircularProgress/>
                                    </div>
                                }
                                <SweetAlert show={goback}
                                    warning
                                    confirmBtnText="Go Back"
                                    confirmBtnBsStyle="danger"
                                    cancelBtnBsStyle="default"
                                    title="Warning!"
                                    onConfirm={() => History.go(-1)}
                                >                
                                Data not cached, so you can not view this page.
                                </SweetAlert>
                            </div>
                        </div>
                    </div>
                </div>
            </AccordionDetails>

          <NotificationContainer/>
        </Accordion>
    )
};

export default Task;