    const resizeFile = (file) => new Promise(resolve => {
        Resizer.imageFileResizer(file, 500, 500, 'JPEG', 100, 0,
        uri => {
          resolve(uri);
        },
        'blob'
        );
    });
    const uploadMultipleFiles= async (e)=>{
        var fileObj = [];
        var img_url = [...room_img_url]
        var send_files = []
        var files = e.target.files
        for(let i = 0; i < files.length; i++) {
            const image = await resizeFile(files[i]);
            send_files.push(new File([image],'room'+i+'.jpg',image))
        }

        fileObj.push(send_files);
        for (let i = 0; i < fileObj[0].length; i++) {
            img_url.push(URL.createObjectURL(fileObj[0][i]));
        }
        setRoomImgUrl(img_url);

        fileObj = [...room_img];
        for(let i = 0; i < send_files.length; i++) {
            fileObj.push(send_files[i]);
        }
        setRoomImg(fileObj);
    }

    const removeImage=(item)=>{
        var img_url = [...room_img_url]
        img_url.splice(item, 1)
        setRoomImgUrl(img_url);
        if(item < room_img_len)
            setRoomImgLen(room_img_len-1);
        else{
            img_url = [...room_img]
            img_url.splice(item-room_img_len, 1)
            setRoomImg(img_url);
        }
    }

