    const ProjectModalSave = () => {
        ProjectModalClose();
        let offline_id = project_id ? project_id:JSON.parse(localStorage.getItem('user')).first_name+'_'+new Date().getTime();
        const frmData = new FormData();
        frmData.append('id', offline_id);
        frmData.append('customer_id', customer_id);
        frmData.append('project_name', project_title);
        frmData.append('manager_id', project_manager);
        frmData.append('contact_number', project_contact);
        frmData.append('assign_to', JSON.stringify(project_access));
        frmData.append('project_summary', project_summary);
        frmData.append('user_id', customer_user);
        frmData.append('upload_doc', project_doc);
        frmData.append('survey_start_date', 
            project_date.getFullYear()+"-"+
            (project_date.getMonth()+1)+"-"+
            project_date.getDate());

        $http.post(base_url + "project/updateProject", frmData)
        .then((response) => {
            if (response.data.status === "success") {
                NotificationManager.success("Update Project", "Success!", 1000, () => {
                });
                if(navigator.onLine)
                Init_data();
            } else if (response.data.status === "error") {
               
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                });
            }
        }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
        })

        if (!navigator.onLine){
            caches.open('SirvezApp').then(function(cache){
                var modify_data = {
                    id : offline_id,
                    customer_id : customer_id,
                    project_name : project_title,
                    manager_id : project_manager,
                    user_id:customer_user,
                    contact_number : project_contact,
                    assign_to : project_access,
                    project_summary : project_summary,
                    manager_name : project_manager_rows.filter((item) => {return (item.id == project_manager)})[0].first_name,
                    user_name : customer_user_rows.filter((item) => {return (item.id == customer_user)})[0].first_name,
                    survey_start_date : project_date.getFullYear()+"-"+(project_date.getMonth()+1)+"-"+project_date.getDate(),
                } 
                cache.match('/api/project/getprojectInfo')
                .then((response)=>response.json())
                .then(function(responseData){
                    responseData.project = modify_data
                    const jsonResponse = new Response(JSON.stringify(responseData), {
                        headers: {
                            'content-type': 'application/json'
                        }
                    });
                    cache.put('/api/project/getprojectInfo?id='+offline_id, jsonResponse.clone());
                })

                cache.match("/api/customers/companyInfo?id="+customer_id)
                .then((response)=>response.json())
                .then(function(responsedata){
                    if(project_id){
                        for (var i = 0; i < responsedata.projects.length; i++) {
                            if (responsedata.projects[i].id === offline_id) {
                                responsedata.projects[i].customer_id = customer_id;
                                responsedata.projects[i].project_name = project_title;
                                responsedata.projects[i].manager_id = project_manager;
                                responsedata.projects[i].user_id = customer_user;
                                responsedata.projects[i].contact_number = project_contact;
                                responsedata.projects[i].assign_to = project_access;
                                responsedata.projects[i].project_summary = project_summary;
                                responsedata.projects[i].survey_start_date = project_date.getFullYear()+"-"+(project_date.getMonth()+1)+"-"+project_date.getDate();
                                responsedata.projects[i].manager_name = project_manager_rows.filter((item) => {return (item.id == project_manager)})[0].first_name;
                                responsedata.projects[i].user_name = customer_user_rows.filter((item) => {return (item.id == customer_user)})[0].first_name;
                                break;
                            }
                        }
                    }
                    else
                    {
                        responsedata.projects.unshift(modify_data)
                    }
                    const jsonResponse = new Response(JSON.stringify(responsedata), {
                        headers: {
                            'content-type': 'application/json'
                        }
                    });
                    cache.put("/api/customers/companyInfo?id="+customer_id, jsonResponse.clone())
                    .then(function(result){
                        Init_data()
                      })
                })
        
            })
        }

    }

