import React from 'react';
import ProjectTableCell from './ProjectTableCell';
import IntlMessages from 'util/IntlMessages';

const ProjectTable = (props) => {

    const {onSelectProject} = props;

    return (
        props.Projects && (
            <div className="table-responsive-material">
                <table className="default-table table-unbordered table table-sm table-hover">
                    <thead className="th-border-b">
                    <tr>
                        <th>Project Title</th>
                        <th>Account Manager</th>
                        <th>End User Contact</th>
                        <th>Survey Date</th>
                        <th>Status</th>
                        <th className="text-right">Actions</th>
                    </tr>
                    </thead>
                    <tbody>
                    {
                        props.Projects.length > 0 ? 
                            props.Projects.map(data => {
                                return (
                                    <ProjectTableCell key={data.id} data={data} onSelectProject={onSelectProject}/>
                                );
                            })
                        :
                            <tr>
                                <td className="text-danger" colSpan="6">
                                    <IntlMessages id="table.noData"/>
                                </td>
                            </tr>

                    }
                    </tbody>
                </table>
            </div>
        )
    );
};

export default ProjectTable;