import React from 'react';
import SiteTableCell from './SiteTableCell';
import IntlMessages from 'util/IntlMessages';

const SiteTable = (props) => {

    const {onSelectSite} = props;

    return (
        props.Sites && (
            <div className="table-responsive-material">
            <table className="default-table table-unbordered table table-sm table-hover">
                <thead className="th-border-b">
                <tr>
                    <th>Site Name</th>
                    <th>City</th>
                    <th>Address1</th>
                    <th>Postcode</th>
                    <th>Rooms</th>
                    <th>Projects</th>
                    <th className="text-right">Actions</th>
                </tr>
                </thead>
                <tbody>
                {
                    props.Sites.length > 0 ?
                        props.Sites.map(data => {
                            return (
                                <SiteTableCell key={data.id} data={data} onSelectSite={onSelectSite}/>
                            );
                        }) 
                    :
                    <tr>
                        <td className="text-danger" colSpan="7">
                            <IntlMessages id="table.noData"/>
                        </td>
                    </tr>
                }
                </tbody>
            </table>
        </div>
        )
    );
};

export default SiteTable;