import React from 'react';
import {SortableContainer, SortableElement, SortableHandle} from 'react-sortable-hoc';
import TaskItem from './TaskItem';
import CustomScrollbars from 'util/CustomScrollbars';

const TaskList = SortableContainer(({Tasks, onTaskSelect, onTaskChecked, onMarkAsStart, width}) => {
   
    return (
        <div className="module-list">
            <CustomScrollbars className="module-list-scroll scrollbar"
                              style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
                {
                    (Tasks.filter(item => item.archived == '0').length > 0) && (
                        <div className="module-list-item bg-info text-white">
                            <div className="module-list-icon">
                                <h2 className="my-0">To Do</h2>
                            </div>
                        </div>
                    )
                }
                {Tasks.filter(item => item.archived == '0').map((Task, index) =>
                    <TaskItem key={index} index={index} Task={Task} onTaskSelect={onTaskSelect}
                              onMarkAsStart={onMarkAsStart}
                              onTaskChecked={onTaskChecked}/>
                )}
                {
                    (Tasks.filter(item => item.archived == '1').length > 0) && (
                        <div className="module-list-item bg-success text-white">
                            <div className="module-list-icon">
                                <h2 className="my-0">Completed</h2>
                            </div>
                        </div>
                    )
                }
                {Tasks.filter(item => item.archived == '1').map((Task, index) =>
                    <TaskItem key={index} index={index} Task={Task} onTaskSelect={onTaskSelect}
                              onMarkAsStart={onMarkAsStart}
                              onTaskChecked={onTaskChecked}/>
                )}
            </CustomScrollbars>
        </div>
    )
});

export default TaskList;