import React, {useState, useEffect} from 'react';
import IconButton from '@material-ui/core/IconButton'
import Input from '@material-ui/core/Input'
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Avatar from '@material-ui/core/Avatar';
import ConversationCell from './ConversationCell';
import CustomScrollbars from 'util/CustomScrollbars';
import {base_url, public_url, $http} from "config";
import CircularProgress from '@material-ui/core/CircularProgress';
import AttachFile from '@material-ui/icons/AttachFile';
import {NotificationContainer, NotificationManager} from 'react-notifications';
import SweetAlert from 'react-bootstrap-sweetalert'
import Resizer from 'react-image-file-resizer';
import Fab from '@material-ui/core/Fab';
import {useHistory} from 'react-router-dom';

const TaskDetail = (props) => {
  const {users, Task, width} = props;
  const [loader, setLoader] = useState(true)
  const [conversation, setConversation] = useState(props.Task.comments)
  const [labelMenu, setLabelMenu] = useState(false)
  const [anchorEl, setAnchorEl] = useState(undefined)
  const [userMenu, setUserMenu] = useState(false)
  const [editTitle, setEditTitle] = useState(false)
  const [notes, setNotes] = useState(props.Task.description)
  const [title, setTitle] = useState(props.Task.task)
  const [editNote, setEditNote] = useState(false)
  const [message, setMessage] = useState('')
  const [user, setUser] = useState( Task.created_by > 0 ? 
    users.find((item) => {return (item.id == Task.created_by)}) : null)
  const me = JSON.parse(localStorage.getItem("user"));
  const [favourite, setFavourite] = useState(Task.favourite == '1')
  const [priority, setPriority] = useState(Task.priority == '1')
  const [archived, setArchived] = useState(Task.archived == '1')
  const [comment_file, setCommentFile] = useState(null)
  const [task_img,setTaskImg] = useState(null)
  const [task_img_url,setTaskImgUrl] = useState(Task.task_img?public_url + 'upload/img/' +Task.task_img:'')
  const [goback, setGoback] = useState(false)
  const History = useHistory();
  // console.log(props.Task.comments)
  // useEffect( () => {
  //   setLoader(true)
  //   $http.get(`${base_url}task/getComments`, {params: {id: Task.id}})
  //   .then((response) => {
  //     if (response.data.comments) {
  //       setConversation(response.data.comments)
  //     }
  //   }).catch((error) => {
  //     setGoback(true)
  //   })
  // }, [])
  useEffect(()=>{
    if (localStorage.getItem('pixie_room_id')) {
      let pixie_room_id = localStorage.getItem('pixie_room_id')
      let pixie_data = localStorage.getItem('pixie_data')
      if (Task.id == pixie_room_id) {
          let arr = pixie_data.split(','),
          mime = arr[0].match(/:(.*?);/)[1],
          bstr = atob(arr[1]), 
          n = bstr.length, 
          u8arr = new Uint8Array(n);

          while (n--) {
              u8arr[n] = bstr.charCodeAt(n)
          }
          let file_name = JSON.parse(localStorage.getItem('user')).id + '-' + new Date().getTime() + '-' + 'pixie.' + mime.split('/').pop()
          let pixie_obj = new File([u8arr], file_name, {type: mime})

          const frmData = new FormData();
          frmData.append('id',Task.id)
          frmData.append('task_img',task_img)
          $http.post(base_url + "task/saveImage", frmData)
          .then((response) => {
              if(response.status = 'success')
              {
                NotificationManager.info(`Task ${Task.task} is updated!`);
              }
              else if (response.data.status === "error") {
                NotificationManager.error(response.data.msg, "Error!", 1000, () => {
                });
              }
          }).catch((error)=>{
            NotificationManager.error(error, "Error!", 1000, () => {
            });
          })
          
          setTaskImg(pixie_obj)

          setTaskImgUrl(URL.createObjectURL(pixie_obj))
      }

      localStorage.removeItem('pixie_room_id')
      localStorage.removeItem('pixie_id')
      localStorage.removeItem('pixie_data')
  }

  },[])

  const handleUserClick = event => {
    setUserMenu(true)
    setAnchorEl(event.currentTarget)
  };
  const handleRequestClose = () => {
    setUserMenu(false)
    setLabelMenu(false)
  };

  const _handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      submitComment();
    }
  };

  const handleEditTitle = () => {
    if (editTitle) {
      Task.task = title
      onTaskUpdate(Task)
    }
    setEditTitle(!editTitle)
  };
  const handleEditNote = () => {
    if (editNote) {
      Task.description = notes;
      onTaskUpdate(Task)
    }
    
    setEditNote(!editNote)
  };
  // const handleDueDateChange = (date) => {
  //   // setTask({...Task, dueDate: date})
  // };

  // const handleChange = (event) => {
  //   // setState({[event.target.name]: event.target.value});
  // };

  const submitComment = () => {
    if (message !== '') {
      const updatedConversation = conversation.concat({
        'first_name': me.first_name,
        'last_name': me.last_name,
        'profile_pic': me.profile_pic,
        'comment': message,
        'created_at': new Date(),
        'attach_file' : comment_file
      });
      setConversation(updatedConversation)
      const frmData = new FormData()
      frmData.append('id', Task.id)
      frmData.append('message', message)
      frmData.append('file', comment_file)
      $http.post(`${base_url}task/commentSubmit`, frmData)
      .then((response) => {
        if (response.data.status === "success") {
          setConversation(response.data.comments)
          setCommentFile(null)
        } else if (response.data.status === "error") {
            NotificationManager.error(response.data.msg, "Error!", 1000, () => {
            });
        }
        
      }).catch((error)=>{
        NotificationManager.error(error, "Error!", 1000, () => {
        });
       })
      setMessage('')
    }
  }

  const updateMessageValue = (evt) => {
    setMessage(evt.target.value)
  }
  const onTaskUpdate = (item) => {
    const frmData = new FormData();
    frmData.append('id', item.id);
    frmData.append('company_id', item.company_id);
    //frmData.append('site_id', site_name);
    frmData.append('project_id', item.project_id);
    frmData.append('room_id', item.room_id);
    frmData.append('task', item.task);
    frmData.append('description', item.description);
    frmData.append('due_by_date', item.due_by_date);
    frmData.append('task_img', item.task_img);
    frmData.append('priority', item.priority);
    frmData.append('created_by', item.created_by);
    $http.post(base_url + "task/updateTask", frmData)
    .then((response) => {
        if(response.data.status === 'success')
          NotificationManager.info(`Task ${item.task} is updated!`);
        else{
          NotificationManager.error(response.data.msg, "Error!", 1000, () => {
          });
        }
         //init_Data();
    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
    })
  }

  const onDeleteTask = (data) => {

  }
  
  const resizeFile = (file) => new Promise(resolve => {
    Resizer.imageFileResizer(file, 500, 500, 'JPEG', 100, 0,
    uri => {
      resolve(uri);
    },
    'blob'
    );
  });

  const uploadImage = async(event)=>{
    var file = event.target.files[0]
    //setTaskImgUrl(event.target.files[0].name);
    const image = await resizeFile(file);
    let img_file_name = file.name
    file = new File([image],img_file_name,image)
    setTaskImgUrl(URL.createObjectURL(file))
    setTaskImg(file);
  }
  const saveTaskImg = ()=>{
    const frmData = new FormData();
    frmData.append('id',Task.id)
    frmData.append('task_img',task_img)
    $http.post(base_url + "task/saveImage", frmData)
    .then((response) => {
        if(response.status = 'success')
        {
          localStorage.setItem('isFirst', 0);
          History.push({
              pathname: `/app/project/sticker`,
              state: {
                  room_id: response.data.id,
                  img_id:response.data.id,
                  img_url: task_img_url,
                  img_file: task_img                
              },
          })
        }
        else if (response.data.status === "error") {
          NotificationManager.error(response.data.msg, "Error!", 1000, () => {
          });
        }
    }).catch((error)=>{
      NotificationManager.error(error, "Error!", 1000, () => {
      });
    })

  }

  return (
    loader ? (
      <div className="module-detail module-list">
        <CustomScrollbars className="module-list-scroll scrollbar"
                          style={{height: width >= 1200 ? 'calc(100vh - 335px)' : 'calc(100vh - 311px)'}}>

          <div className="module-detail-item module-detail-header">
            <div className="row">
              <div className="col-sm-6 col-md-8 col-12">
                <div className="d-flex align-items-center">
                  <div className="user-name mr-md-4 mr-2"
                       onClick={handleUserClick}>

                    {user === null ? <h4 className="mb-0 mouse pointer">Assign User </h4> :
                      <div className="d-flex align-items-center pointer">
                        <Avatar className="mr-2" src={public_url + 'upload/img/' + user.profile_pic} alt={user.first_name}/>
                        <h4 className="mb-0">{user.first_name} {user.last_name}</h4>
                      </div>}
                  </div>
                  {/* <DatePicker className="module-date"
                    value={Task.dueDate}
                    invalidLabel="Due Date"
                    onChange={this.handleDueDateChange.bind(this)}
                    animateYearScrolling={false}
                    leftArrowIcon={<i className="zmdi zmdi-arrow-back"/>}
                    rightArrowIcon={<i className="zmdi zmdi-arrow-forward"/>}
                    InputProps={{
                      endAdornment: (
                        <IconButton className="icon-btn">
                          <i className="zmdi zmdi-calendar"/>
                        </IconButton>
                      ),
                    }}
                  /> */}
                </div>
              </div>
              <div className="col-sm-6 col-md-4 col-6">
                <div
                  className="d-flex flex-sm-row-reverse justify-content-between justify-content-sm-start">
                  <Menu
                    id="label-menu"
                    anchorEl={anchorEl}
                    open={userMenu}
                    onClose={handleRequestClose}

                    MenuListProps={{
                      style: {
                        width: 180,
                      },
                    }}>

                    {users.map((user, index) =>
                      <MenuItem key={index} value={user.id} onClick={() => {
                        handleRequestClose();
                        Task.created_by = user.id;
                        onTaskUpdate(Task)
                        setUser(user)
                      }}>
                        <div className="d-flex user-name manage-margin align-items-center">
                          <Avatar src={public_url + 'upload/img/' + user.profile_pic} 
                            alt={user.first_name}/>
                          <h4>{user.first_name} {user.last_name}</h4>
                        </div>
                      </MenuItem>
                    )}

                  </Menu>

                  {/* <IconButton className="icon-btn" onClick={() => {
                    onDeleteTask(Task);
                  }}>
                    <i className="zmdi zmdi-delete"/>
                  </IconButton> */}

                  <IconButton className="icon-btn" onClick={() => {
                    Task.favourite = Task.favourite == '1' ? '0' : '1';
                    setFavourite(!favourite)
                    onTaskUpdate(Task);
                  }}>
                    {Task.favourite == '1' ?
                    <span className="size-30 rounded-circle text-green border-green">
                      <i className="zmdi zmdi-star text-warning"/> 
                    </span>
                      :
                    <span className="size-30 rounded-circle text-green border-green">
                      <i className="zmdi zmdi-star-outline text-warning"/>
                    </span>
                    }

                  </IconButton>

                  <IconButton className="icon-btn" onClick={() => {
                    Task.priority = Task.priority == '1' ? '0' : '1';
                    setPriority(!priority)
                    onTaskUpdate(Task);
                  }}>
                    {Task.priority == '1' ?
                    <span className="size-30 rounded-circle text-green border-green">
                      <i className="zmdi zmdi-info"/>
                    </span>
                       :
                    <span className="size-30 rounded-circle text-green border-green">
                      <i className="zmdi zmdi-info-outline"/>
                    </span>
                    }

                  </IconButton>
                  <IconButton className="icon-btn" onClick={() => {
                    Task.archived = Task.archived == '1' ? '0' : '1';
                    setArchived(!archived)
                    onTaskUpdate(Task)
                  }}>
                    {Task.archived == '1' ?
                      <span className="border-2 size-30 rounded-circle text-green border-green">
                        <i className="zmdi zmdi-check"/></span> :
                      <span className="border-2 size-30 w-2 rounded-circle text-muted border-grey">
                        <i className="zmdi zmdi-check"/></span>
                    }
                  </IconButton>
                </div>
              </div>
            </div>
          </div>

          <div className="module-detail-item">

            <div className="labels mb-4">
              {/* {labels.map((label, index) => {
                return (Task.labels).includes(label.id) && <div key={index}
                                                                className={`badge text-white bg-${label.color}`}>{label.title}</div>
              })} */}
              <div className="badge text-white badge-primary">{Task.company_name}</div>
              <div className="badge text-white badge-secondary">{Task.project_name}</div>
              {/* <div className="badge text-white badge-success">{Task.site_name}</div> */}
              <div className="badge text-white badge-danger">{Task.building_name}</div>
              <div className="badge text-white badge-warning">{Task.floor_name}</div>
              <div className="badge text-white badge-info">{Task.room_number}</div>
            </div>

            <div className="form-group d-flex align-items-center mb-0">
              <IconButton className="icon-btn p-1">
                <i className="zmdi zmdi-menu"/>
              </IconButton>

              {editTitle ? <div className="d-flex align-items-center w-100">
                  <div className="col">
                    <Input
                      fullWidth
                      multiline className="task-title"
                      id="required"
                      placeholder="Title"
                      onChange={(event) => setTitle(event.target.value)}
                      defaultValue={title}/>
                  </div>

                  <IconButton onClick={handleEditTitle}>
                    <i className="zmdi zmdi-check"/>
                  </IconButton>
                </div> :
                <div className="d-flex align-items-center w-100">
                  <div className="task-title col">
                    {title}
                  </div>
                  <IconButton className="icon-btn" onClick={handleEditTitle}>
                    <i className="zmdi zmdi-edit"/>
                  </IconButton>

                </div>}
            </div>


          </div>

          <div className="module-detail-item mb-4">
            {editNote ? <div className="d-flex align-items-center w-100">
                <Input
                  fullWidth
                  id="required"
                  multiline
                  placeholder="Note"
                  onChange={(event) => setNotes(event.target.value)}
                  defaultValue={notes}/>

                <IconButton className="icon-btn" onClick={handleEditNote}>
                  <i className="zmdi zmdi-check"/>
                </IconButton>
              </div> :
              <div className="d-flex align-items-center w-100">
                <div className="task-des col px-0"
                dangerouslySetInnerHTML={{__html: notes ? notes.replace(/\n/g, '<br>') : ''}}/>
               <IconButton className="icon-btn" onClick={handleEditNote}>
                  <i className="zmdi zmdi-edit"/>
                </IconButton>
              </div>}
          </div>
          <div className="bg-primary text-center w-100 mt-4 mb-4">
              <input type="file" id="upload_doc" 
                  accept="image/*" 
                  name="upload_doc" style={{display: "none"}}
                  onChange={(event) => {
                      uploadImage(event)
                  }}/>
                <IconButton className="text-grey"
                  variant="contained" onClick={(e)=>{document.getElementById('upload_doc').click()}}>
                  <i className="zmdi zmdi-camera zmdi-hc-2x animated infinite pulse"/>
              </IconButton>
              {/* <h2>{TaskImgUrl }</h2> */}
          </div>
          {task_img_url?
          <div className="col-12">
            <div className="jr-card pb-2">
                <div className="jr-card-thumb">
                    <img className="card-img-top img-fluid" alt="products" src={task_img_url}/>
                </div>
                
                <div className="jr-card-social text-center">
                    <div className = "row">
                        <div className="col-xl-6 col-lg-6 col-md-6 col-6">
                            <Fab className="jr-fab-btn bg-light-blue accent-2 text-white" 
                                onClick = {() => saveTaskImg()}
                                >
                                <i className="zmdi zmdi-puzzle-piece zmdi-hc-2x"/>
                            </Fab>
                        </div>
                        <div className="col-xl-6 col-lg-6 col-md-6 col-6">
                            <Fab className="jr-fab-btn bg-red accent-2 text-white"  
                                onClick = {()=>{setTaskImg(null)
                                    setTaskImgUrl('')}}
                                        >
                                <i className="zmdi zmdi-delete zmdi-hc-2x"/>
                            </Fab>
                        </div>
                    </div>
                </div>
            </div>
        </div>
          :null
          }

          <div className="module-detail-item">
            <h3>Comments</h3>
          </div>
          {conversation.map((item, index) =>
            <ConversationCell key={index} conversation={item}/>
          )}


        </CustomScrollbars>

        <div className="chat-main-footer todo-main-footer">
          <div className="d-flex flex-row align-items-center" style={{maxHeight: 61}}>
            <div className="col">
              <div className="form-group">
                <textarea className="border-0 form-control chat-textarea"
                            id="required"
                            onKeyUp={_handleKeyPress.bind(this)}
                            onChange={updateMessageValue.bind(this)}
                            value={message}
                            rows={2}
                            placeholder="Type and hit enter to send message"/>
              </div>
            </div>
            <div className="chat-sent">
              <input type="file" id="upload_doc" 
                  accept="image/*" 
                  name="upload_doc" style={{display: "none"}}
                  onChange={(event) => {
                      setCommentFile(event.target.files[0]);
                  }}/>

              <IconButton
                onClick={(e) => document.getElementById("upload_doc").click()}
                aria-label="Send message"
                className={comment_file == null ? `` : `text-success`}>
                <AttachFile/>
              </IconButton>
              <IconButton
                onClick={() => {submitComment()}}
                aria-label="Send message">
                <i className="zmdi  zmdi-mail-send"/>
              </IconButton>
            </div>
          </div>
        </div>
        
      </div>

    ):(
      
      <div className="loader-view"
        style={{height: width >= 1200 ? 'calc(100vh - 259px)' : 'calc(100vh - 238px)'}}>
        <CircularProgress/>
        <SweetAlert show={goback}
            warning
            confirmBtnText="Go Back"
            confirmBtnBsStyle="danger"
            cancelBtnBsStyle="default"
            title="Warning!"
            onConfirm={() => History.go(-1)}
        >                
        Data not cached, so you can not view this page.
        </SweetAlert>
        <NotificationContainer/>
      </div>
    )
    
  );
}

export default TaskDetail;
